@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,400;0,700;0,1000;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  width: 100vw;
  background-color: #002a5c;
  margin-bottom: 30px;

}

.nav-item a {
  color: white;
  font-weight: bold;
  font-size: 20px;
}

.nav-item a:hover {
  color: #c4d1e4;
}

.navbar-brand img {
  margin-left: 20px;
  max-width: 50px;
}

.body_text {
  font-family: 'Times New Roman', Times, serif;
  position: relative;
  font-size: 150%;
  margin-left: 40px;
  margin-right: 50px;
  margin-top: 170px;
  margin-bottom: 10px;

}

